<template>
  <div class="restrict publication">
    <div class="row">
      <div class="maxTitle">
        <div>PUBLICATION</div>
      </div>
      <ul class="time-axis">
        <li class="time-axis-item" v-for="(i, j) in publications" :key="j">
          <div class="time-axis-date"><span></span></div>
          <div class="time-axis-title">
            <div class="date">{{ i.time }}</div>
            <div class="notepad" v-for="(m, n) in i.content" :key="n">
              {{ m }}
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { getPublication } from '@/api/video/index.js'
export default {
  data: () => ({
    publications: []
  }),
  created() {
    getPublication().then( res => {
      res.data.sort((a, b) => b.Stime - a.Stime)
      this.publications = res.data.map(v => ({
        time: v.Stime,
        content: v.Contents.split('</p><p>')
      }))
      console.log(this.publications.sort((a, b) => a.Stime - b.Stime))
    })
  }
};
</script>
